import React from "react"
import { useSievedUserBriefConfigs } from "@rp/react/common/brief"
import { BriefConfig } from "@rp/react/tanstack-api/brief"
import { LoadingSpinner } from "@rp/react/web/common/base-components"
import { Box } from "@rp/react/web/mui/core"
import { BriefDashConfigsListHasCards } from "./BriefDashConfigsListHasCards"
import { BriefDashConfigsListNoneCreated } from "./BriefDashConfigsListNoneCreated"
import { BriefDashConfigsListNoneSieved } from "./BriefDashConfigsListNoneSieved"

type BriefDashConfigsListProps = {
  userBriefConfigs: Array<BriefConfig> | undefined
  filterTerm: string
}

const BriefDashConfigsList = ({ userBriefConfigs, filterTerm }: BriefDashConfigsListProps) => {
  const { sievedUserBriefConfigs } = useSievedUserBriefConfigs({ userBriefConfigs, filterTerm })

  const isLoading = !userBriefConfigs
  const hasNoConfigsCreated = !isLoading && userBriefConfigs?.length === 0
  const hasNoSievedConfigs = !hasNoConfigsCreated && sievedUserBriefConfigs.length === 0
  const hasConfigsToShow = sievedUserBriefConfigs.length > 0

  if (isLoading) return <LoadingSpinner />

  return (
    <Box
      display="flex"
      alignItems="center"
      flexDirection="column"
      role="region"
      aria-label="briefs list container"
    >
      {hasNoConfigsCreated && <BriefDashConfigsListNoneCreated />}

      {hasNoSievedConfigs && <BriefDashConfigsListNoneSieved />}

      {hasConfigsToShow && (
        <BriefDashConfigsListHasCards
          sievedUserBriefConfigs={sievedUserBriefConfigs}
          isFiltering={!!filterTerm}
        />
      )}
    </Box>
  )
}

export { BriefDashConfigsList }
